.header__toggle {
  position: relative;
  top: 5px;
  right: 23px;
  height: 24px;
  padding-top: 3px;
  padding-left: 30px;
  border: none;
  cursor: pointer;
  background-color: transparent;
}
@media (min-width: 768px) {
  .header__toggle {
    display: none;
  }
}

.header__toggle--close::before {
  content: "";
  position: absolute;
  top: 1px;
  left: 10px;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}

.header__toggle--open {
  position: relative;
  top: 5px;
  right: 23px;
  height: 24px;
  padding-top: 3px;
  padding-left: 30px;
  border: none;
  cursor: pointer;
  background-color: transparent;
}
@media (min-width: 768px) {
  .header__toggle--open {
    display: none;
  }
}
.header__toggle--open::before, .header__toggle--open::after {
  content: "";
  position: absolute;
  top: 1px;
  left: 10px;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
.header__toggle--open::before {
  transform: rotate(45deg);
}
.header__toggle--open::after {
  transform: rotate(-45deg);
}

.header__toggle:active {
  opacity: 30%;
}

.header__logo-text {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
